:root {
  --bg-white: #fff;
  --bg-dark-gray: #3e3e3e;
  --fnt-dark-gray: #3e3e3e;
  --fnt-light-blue: #76bae7;
}

body {
  font-family: Montserrat, sans-serif;
}

.container {
  max-width: 1700px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.navbar-navigation-icon {
  cursor: pointer;
  max-width: 35px;
  max-height: 25px;
  padding: 3px;
}

.sidenav {
  z-index: 11000;
  background-color: var(--bg-dark-gray);
  width: 330px;
  height: calc(100vh - 41px);
  padding: 2rem;
  transition: all .4s ease-in-out;
  position: absolute;
  left: -450px;
  box-shadow: 15px 0 20px #000000a6;
}

.sidenav.show {
  left: 0;
}

.sidenav .sidenav-container .nav-link {
  color: var(--bg-white);
  font-size: .8rem;
}

.sidenav .sidenav__list {
  flex-direction: column;
  display: flex;
}

.sidenav .sidenav__list li {
  border-bottom: 1px solid #fff3;
  align-items: center;
  height: 60px;
  display: flex;
}

.navbar {
  background-color: var(--bg-white);
}

.navbar .nav-link {
  text-align: right;
  flex-grow: 1;
}

.navbar .nav-link.ventajas {
  text-align: left;
  flex-grow: 2;
}

.navbar .navbar-nav {
  justify-content: space-between;
  width: 100%;
}

.navbar .nav-link {
  color: var(--fnt-dark-gray);
}

.navbar .nav-link:hover {
  font-weight: bold;
}

.navigation .nav-link.active {
  color: var(--fnt-light-blue);
}

@media screen and (width <= 992px) {
  .navbar {
    background-color: var(--bg-dark-gray);
  }

  .navbar .container-fluid {
    justify-content: flex-end;
    padding-inline: 0;
    display: flex;
  }

  .navbar-nav {
    display: none;
  }
}

@media screen and (width >= 993px) {
  .navbar-navigation-icon, .sidenav {
    display: none;
  }
}

@media screen and (width <= 1015px) {
  .navbar {
    font-size: .9rem;
  }
}

